.bg-video{
   position: absolute;
   top: 0;
   left: 0;
   height: 100%;
   width: 100%;
   z-index: -1;
   opacity: .25;
   background-size: cover;
   
   &__content{
       width: 100%;
       height: 100%;
       top: 0;
       left: 0;
       object-fit: cover;
       background-size: cover;
       position: relative;
       
   }

}

