
.grid{
    display: flex;  
    flex-flow:row wrap;
    padding:3rem;
    width: 100%;
    height: auto;    
    box-sizing: border-box;
    position: relative;
    justify-content: center;
      

    @include respond(tab-port){
        justify-content: center;       
        flex-flow: column wrap !important;   
        }

    .grid-text{
       flex-basis: 40%;
       height: auto;
       position: relative;
       height: auto;

       a{
           margin-bottom: 5%;
       }

       @include respond(tab-port){
        
        flex-basis: 100% !important;   
        padding: 1rem 4rem;
       }

    }
    
    .grid-img{ 
        margin-right: 5%;
        position: relative;
        height: auto;
        flex-basis: 40%;
        
        @include respond(tab-port){
            flex-basis: 100% !important;
             justify-self: center;
            padding:2rem 4rem 2rem 6rem;
            margin-right: 0 !important;
        }
    
        
    }
    
}