
@keyframes moveInLeft{

    0%{
       opacity: 0; 
       transform: translateX(-10rem);
    } 
    
    100%{
        opacity: 1;
        transform: translateX(0);
    }
    }
    
    @keyframes moveInRight{
    
    0%{
       opacity: 0; 
       transform: translateX(10rem);
    }
    
    100%{
        opacity: 1;
        transform: translateX(0);
    }
    }
    
    @keyframes moveInUp{
    
    0%{
       opacity: 0; 
       transform: translateY(3rem);
    }
    
    100%{
        opacity: 1;
        transform: translateY(0);
    }
    }
    